    function GenerateSegmentsDataFromString(stringData: string): Array<Segment> {
        const texts = stringData.split('\n');

        let result = new Array<Segment>(texts.length);

        for (let i: number = 0; i < texts.length; ++i) {
            result[i] = new Segment(texts[i], Translate(texts[i]));
        }

        return result;
    }

    async function GenerateAudio() {
        //zapisac zmiany, 30 na sekunde, URL.create

        const newSegmentsData = segmentsData.slice();
        console.log(newSegmentsData);

        const sendRequests = new Array<Promise<Blob>>();

        for (let i: number = 0; i < newSegmentsData.length; ++i) {
            sendRequests.push(SendRequestForSpeech(newSegmentsData[i].word));
        }

        const blobs = await Promise.all(sendRequests);

        for (let i: number = 0; i < newSegmentsData.length; ++i) {
            newSegmentsData[i].pathToVoice = URL.createObjectURL(blobs[i]);

            //console.log(segmentsData[i].pathToVoice);
        }

        setSegmentsData(newSegmentsData);
    }

    async function SendRequestForSpeech(text: string):Promise<Blob> {
        const options = {
            method: 'GET',
            url: 'https://text-to-speech-api3.p.rapidapi.com/speak',
            params: {
              text: text,
              lang: 'en',
            },
            headers: {
              'X-RapidAPI-Key': tokens.speech,
              'X-RapidAPI-Host': 'text-to-speech-api3.p.rapidapi.com'
            },
            responseType: 'arraybuffer'
          } as any;
        
          try {
            const audiobufferToBlob = require('audiobuffer-to-blob');

            const response = await axios.request(options);
            const ctx = new AudioContext();
            const decode = await ctx.decodeAudioData(response.data);
            const blob = audiobufferToBlob(decode);

            return new Promise<Blob>((resolve, reject) => {resolve(blob)});
        } catch (error) {
            console.error(error);
            return new Promise<Blob>((resolve, reject) => {reject(error)});
        }
    }

